.ecl-wallet {
  $border-radius: 30px;
  width: 100%;
  margin: 0 auto 32px auto;
  height: 60px;
  display: grid;
  grid-template-columns: 1fr 65px 1fr;
  align-items: center;

  &__item {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    font-weight: 600;

    &--money {
      margin-right: -30px;
      border-radius: $border-radius;
      color: var(--ion-color-light);
      background: var(--ion-color-primary);
      padding-right: 10px;
    }

    &--icon {
      height: 100%;
      width: 65px;
      z-index: 2;
      border-radius: 50%;
      font-size: 44px;
      background: #f2f2f2;
    }

    &--time {
      margin-left: -30px;
      padding-left: 10px;
      border-radius: $border-radius;
      color: var(--ion-color-light);
      background: var(--ion-color-primary-shade);
    }
  }
}
